.register {
  max-width: 1200px;
  position: relative;
  margin: 20px auto;
}

.register img {
  width: 100%;
  height: 500px;
}

.register .form {
  position: absolute;
  top: 20px;
  left: 20%;
  z-index: 15;
  width: 400px;
  height: 450px;
  border: 3px solid rgba(0, 0, 0, 0.285);
  border-radius: 10px;
  box-shadow: 3px 3px 5px 5px rgba(0, 0, 0, 0.134);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.register .form input {
  width: 90%;
  margin-bottom: 20px;
  padding: 5px 10px;
  font-size: 18px;
}

.register .form button {
  color: #fff;
  background-color: #333;
  padding: 10px 30px;
  border-radius: 5px;
  font-size: 16px;
  display: block;
  margin: 20px 0;
}

.register .form button:hover {
  cursor: pointer;
}

.register .form h1 {
  margin: 20px 0;
}

@media (max-width: 768px) {
  .register {
    background-color: rgba(51, 51, 51, 0.068);
    padding: 20px 0;
  }
  .register img {
    display: none;
  }

  .register .form {
    position: relative;
    top: 0;
    left: 50%;
    transform: translate(-50%);
  }
}
